  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 5000,
      position: 'bottom'
    });
    toast.present();
  }
 
  public chooser() {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Select Files',
      buttons: [
        {
          text: 'Load from Library',
          handler: () => {
            this.fileChooser.open()
              .then(uri => {
                console.log(uri)
                this.filePath.resolveNativePath(uri)
                  .then(filePath => {
                    this.filenewpath = filePath;
                    let correctPath = filePath.substr(0, filePath.lastIndexOf('/') + 1);
                    let currentName = filePath.substring(filePath.lastIndexOf("/") + 1);

                    this.firstfilename = currentName;
                    
                    var yy=currentName;
                    var re = /(?:\.([^.]+))?$/;
                    var ext = re.exec(yy)[1];
                    this.firstfilepath = filePath;
                    console.log(ext);

                    this.images.push({path:this.filenewpath,name:currentName,ext:ext});

/* 
                    if(ext=='pdf' || ext=='png' || ext=='jpg' || ext=='jpeg' ){
                      this.images.push({path:this.filenewpath,name:currentName});

                      
                    }
                    else{
                    }
 */
                    if(this.images.length==0){
                      this.fileshow=0;
                          }
                          else{
                            this.fileshow=1;
                      
                          }
                    console.log(correctPath);
                    console.log(filePath);
                    console.log(currentName);
                    console.log(uri);


                  });

                console.log(this.firstfilepath);
              }
              )
              .catch(e => console.log(e));


          }
        },
               {
          text: 'Cancel',
          role: 'cancel'
        }
      ]
    });
    actionSheet.present();
  } 


  public uploadImage1(cid) {
    console.log("test");

    
    // Destination URL
/*      var url = "https://forms.thk.org.sg/api/inserthc_ccsfform";

 */

var url = "http://sputnikinfotech.com/nms/index.php/api/nms_photo";

 
 for (let i = 0; i <  this.images.length; i++) {

    // File for Upload
    var targetPath = this.firstfilepath;
    console.log("haifirst" + targetPath);

    // File name only
    var filename = this.firstfilename;

    var options = {
      fileKey: "file",
      fileName: filename,
      chunkedMode: false,
      mimeType: "multipart/form-data",
      params: { 'file': this.firstfilename,ccid
      : cid,attachment:this.images[i].name,id:this.caste}
    };
    console.log(filename);

    const fileTransfer: FileTransferObject = this.transfer.create();

    /*    this.loading = this.loadingCtrl.create({
         content: 'Uploading...',
       });
       this.loading.present(); */
    console.log(targetPath);
    console.log(url);
    this.image = url;
    // Use the FileTransfer to upload the image
    fileTransfer.upload(this.images[i].path, url, options).then(data => {
      console.log("cuming");

      console.log(data);
/*       this.loading.dismissAll()
 */     /*  this.presentToast('File succesful uploaded.'); */

    }, err => {
      console.log(err);
      console.log("eee");


/*       this.loading.dismissAll()
 */     /*  this.presentToast('Error while uploading file.'); */
    });
  }
  }

